<template>
<div :style='{"width":"100%","padding":"30px 7% 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <el-form
	  :style='{"border":"1px solid #f0f0f0","width":"100%","padding":"30px","position":"relative","background":"#fcfcfc"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="茶叶名称" prop="chayemingcheng">
            <el-input  v-model="ruleForm.chayemingcheng" 
                placeholder="茶叶名称" clearable :readonly=" false  ||ro.chayemingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="种植数量" prop="zhongzhishuliang">
            <el-input  v-model.number="ruleForm.zhongzhishuliang" 
                placeholder="种植数量" clearable :readonly=" false  ||ro.zhongzhishuliang"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="种植日期" prop="zhongzhiriqi">
              <el-date-picker
				  :disabled=" false  ||ro.zhongzhiriqi"
                  format="yyyy 年 MM 月 dd 日"
                  value-format="yyyy-MM-dd"
                  v-model="ruleForm.zhongzhiriqi" 
                  type="date"
                  placeholder="种植日期">
              </el-date-picker> 
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="主管账号" prop="zhuguanzhanghao">
            <el-input  v-model="ruleForm.zhuguanzhanghao" 
                placeholder="主管账号" clearable :readonly=" false  ||ro.zhuguanzhanghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="主管姓名" prop="zhuguanxingming">
            <el-input  v-model="ruleForm.zhuguanxingming" 
                placeholder="主管姓名" clearable :readonly=" false  ||ro.zhuguanxingming"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="种植说明" prop="zhongzhishuoming">
            <editor 
                :style='{"minHeight":"250px","padding":"0","margin":"0","borderColor":"#1abc9e30","backgroundColor":"none","borderRadius":"0","borderWidth":"0px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.zhongzhishuoming" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"0px","background":"#1abc9e","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"1px solid #eee","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#333","borderRadius":"0px","background":"#fcfcfc","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            chayemingcheng : false,
            zhongzhishuliang : false,
            zhongzhiriqi : false,
            zhongzhishuoming : false,
            zhuguanzhanghao : false,
            zhuguanxingming : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          chayemingcheng: '',
          zhongzhishuliang: '',
          zhongzhiriqi: '',
          zhongzhishuoming: '',
          zhuguanzhanghao: '',
          zhuguanxingming: '',
        },


        rules: {
          chayemingcheng: [
          ],
          zhongzhishuliang: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          zhongzhiriqi: [
          ],
          zhongzhishuoming: [
          ],
          zhuguanzhanghao: [
          ],
          zhuguanxingming: [
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.zhongzhiriqi = this.getCurDate()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='chayemingcheng'){
              this.ruleForm.chayemingcheng = obj[o];
              this.ro.chayemingcheng = true;
              continue;
            }
            if(o=='zhongzhishuliang'){
              this.ruleForm.zhongzhishuliang = obj[o];
              this.ro.zhongzhishuliang = true;
              continue;
            }
            if(o=='zhongzhiriqi'){
              this.ruleForm.zhongzhiriqi = obj[o];
              this.ro.zhongzhiriqi = true;
              continue;
            }
            if(o=='zhongzhishuoming'){
              this.ruleForm.zhongzhishuoming = obj[o];
              this.ro.zhongzhishuoming = true;
              continue;
            }
            if(o=='zhuguanzhanghao'){
              this.ruleForm.zhuguanzhanghao = obj[o];
              this.ro.zhuguanzhanghao = true;
              continue;
            }
            if(o=='zhuguanxingming'){
              this.ruleForm.zhuguanxingming = obj[o];
              this.ro.zhuguanxingming = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.zhuguanzhanghao!=''&&json.zhuguanzhanghao) || json.zhuguanzhanghao==0){
                this.ruleForm.zhuguanzhanghao = json.zhuguanzhanghao
            }
            if((json.zhuguanxingming!=''&&json.zhuguanxingming) || json.zhuguanxingming==0){
                this.ruleForm.zhuguanxingming = json.zhuguanxingming
            }
          }
        });
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`chayezhongzhi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

        //更新跨表属性
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('chayezhongzhi/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 跨表计算


                          this.$http.post(`chayezhongzhi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '操作成功',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {


                  this.$http.post(`chayezhongzhi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '操作成功',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // 获取uuid
      getUUID () {
        return new Date().getTime();
      },
      // 返回
      back() {
        this.$router.go(-1);
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.el-date-editor.el-input {
		width: auto;
	}
	
